.footer {
  background-color: #252422;
  min-height: 22.5rem;
  width: 100%;
  bottom: 0;
  position: relative;
}
.footer__title {
  font-size: 1.875rem;
  letter-spacing: 0.0625rem;
  font-weight: 400;
}
.footer .img {
  width: 6.25rem;
  height: 2.1875rem;
}
.footer .logo {
  letter-spacing: 0;
  font-weight: 600;
}
.footer svg {
  fill: white;
}

html {
  overflow-x: none;
  font-size: 100%;
}

header {
  background-color: darkgray;
  height: 23.75rem;
  margin-bottom: 5.625rem;
}
header a {
  text-decoration: none;
  color: black;
}
header svg {
  fill: black;
}

body .title {
  font-size: 22px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 7px;
}
body .secondary-title {
  color: #777;
  font-size: 11px;
  font-weight: 300;
  letter-spacing: 7px;
  text-transform: uppercase;
  margin-bottom: 21px;
}

section, footer {
  padding-right: 19.35vw;
  padding-left: 19.35vw;
}

.topOne__call {
  margin-right: 0.625rem;
}
.topOne__icon {
  padding-top: 0.625rem;
  padding-right: 2.375rem;
}
.topOne__last {
  padding-top: 0.625rem;
}
.topOne .vr {
  margin-left: 1.875rem;
  margin-right: 1.875rem;
}

.bottomOne__logo {
  width: 12.5rem;
}
.bottomOne__span {
  margin-top: 7.1875rem;
}

.copyright {
  padding-right: 19.35vw;
  padding-left: 19.35vw;
  color: #686a5f;
  background-color: #141412;
  min-height: 3.75rem;
}

.contact {
  min-height: 12.5rem;
  background-color: #a39064;
}
.contact p {
  font-size: 2.5rem;
  margin-bottom: 3.75rem;
  margin-top: 3.75rem;
}
.contact .btn {
  border: 1px solid white;
  color: white;
  min-width: 11.875rem;
  font-size: 0.8125rem;
  font-weight: 600;
}

.row {
  display: flex;
}

.col-1 {
  flex: 0 0 8.3333333333%;
}

.col-2 {
  flex: 0 0 16.6666666667%;
}

.col-3 {
  flex: 0 0 25%;
}

.col-4 {
  flex: 0 0 33.3333333333%;
}

.col-5 {
  flex: 0 0 41.6666666667%;
}

.col-6 {
  flex: 0 0 50%;
}

.col-7 {
  flex: 0 0 58.3333333333%;
}

.col-8 {
  flex: 0 0 66.6666666667%;
}

.col-9 {
  flex: 0 0 75%;
}

.col-10 {
  flex: 0 0 83.3333333333%;
}

.col-11 {
  flex: 0 0 91.6666666667%;
}

.col-12 {
  flex: 0 0 100%;
}

.circle {
  margin-right: 0.625rem;
  font-size: 1.125rem;
  font-weight: 700;
  color: white;
  height: 1.875rem;
  width: 1.875rem;
  background-color: #4b4b4b;
  border-radius: 3.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.circle svg,
.circle img {
  fill: white;
  height: 0.9375rem;
  width: 0.9375rem;
}
.circle:hover {
  background-color: #0d0d0d;
}

.description {
  margin-bottom: 5.625rem;
}
.description__paragraph {
  margin-bottom: 3.125rem;
}

.form {
  margin-bottom: 1.25rem;
}
.form__img {
  width: 48.125rem;
}
.form__right {
  margin-left: 1.875rem;
}
.form__title {
  font-size: 22px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 7px;
}
.form__group {
  margin-bottom: 4.0625rem;
}
.form__group--title {
  text-transform: uppercase;
  font-size: 0.8125rem;
  font-weight: 700;
}
.form__group--smallTitle {
  margin-bottom: 0.75rem;
  font-size: 0.8125rem;
  text-transform: capitalize;
  color: #4b4b4b;
}

@media (min-width: 20em) {
  .form__img {
    min-height: 20rem;
  }
}
.custom-border {
  background-color: black;
  height: 0.125rem;
  width: 5.625rem;
  margin-top: 1.875rem;
  margin-bottom: 1.875rem;
}

.arrow {
  border: solid white;
  border-width: 0 0.1875rem 0.1875rem 0;
  display: inline-block;
  padding: 0.1875rem;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.card {
  height: auto;
  min-width: auto;
}
.card .card-img {
  height: auto;
}

.card-big {
  border: 0;
  height: 29.375rem;
  min-width: 1.15625rem;
}
.card-big .card-img {
  height: 29.375rem;
}

.btn {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: transparent;
}

.btn-primary {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: #ffcc00;
  background-color: #ffcc00;
}
.btn-primary:hover {
  background-color: #ffd633;
}

.btn-outlined-primary {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: transparent;
  border: 0.0625rem solid #ffcc00;
}
.btn-outlined-primary:hover {
  background-color: #ffcc00;
}

.btn-secondary {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: #4b4b4b;
  background-color: #4b4b4b;
}
.btn-secondary:hover {
  background-color: #656565;
}

.btn-outlined-secondary {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: transparent;
  border: 0.0625rem solid #4b4b4b;
}
.btn-outlined-secondary:hover {
  background-color: #4b4b4b;
}

.btn-dark {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: black;
  background-color: black;
}
.btn-dark:hover {
  background-color: #1a1a1a;
}

.btn-outlined-dark {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: transparent;
  border: 0.0625rem solid black;
}
.btn-outlined-dark:hover {
  background-color: black;
}

.btn-white {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: white;
  background-color: white;
}
.btn-white:hover {
  background-color: white;
}

.btn-outlined-white {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: transparent;
  border: 0.0625rem solid white;
}
.btn-outlined-white:hover {
  background-color: white;
}

.btn-muted {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: rgba(255, 255, 255, 0.5);
  background-color: rgba(255, 255, 255, 0.5);
}
.btn-muted:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.btn-outlined-muted {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 10px;
  background-color: #fff;
  background: transparent;
  border: 0.0625rem solid rgba(255, 255, 255, 0.5);
}
.btn-outlined-muted:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0;
}

h1 {
  font-size: clamp(1.75rem, 1rem + 2vw, 3rem);
}

p {
  margin-top: 0;
}

a, a:visited, active {
  text-decoration: none;
}

.group__small {
  margin-bottom: 160px;
}
.group__big {
  margin-bottom: 90px;
}